
<template>
  <div id="app">
    <!-- <img src="./assets/logo.png">
    <router-view/> -->
    <button @click="goHistoricalNews">跳转公共号历史消息</button><br/>
    <button @click="goXcx">跳转小程序</button><br/>
  </div>
</template>

<script>
export default {
  name: 'App',
  data(){
    return{
      historicalNewsUrl:"https://mp.weixin.qq.com/mp/profile_ext?action=home&__biz=MzA4ODE5OTEzNA==&scene=124#wechat_redirect",
      xcxUrl:"https://mp.weixin.qq.com/s/veOkHmuADI5Ddu57HTVYRw"
    }
  },
  methods:{
    goHistoricalNews:function(){
      location.href=this.historicalNewsUrl
    },
    goXcx:function(){
      location.href=this.xcxUrl
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



